@import url(https://fonts.googleapis.com/css?family=Crimson+Text:400,700);
html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: 'Crimson Text', serif;
  background-color: #eff0f0;
  color: #404949;
}

#root {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

#game-title {
  font-size: 26px;
  border-bottom: 1px solid #bcbcbc;
  border-radius: 2px;
  background: rgba(0, 0, 0, .1);
  height: 38px;
  min-height: 38px;
}

.flex {
  display: -webkit-flex;
  display: flex;
}

.flex-col {
  -webkit-flex-direction: column;
          flex-direction: column;
}

.flex-row {
  -webkit-flex-direction: row;
          flex-direction: row;
}

.flex-center {
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.flex-fill {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

#square {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.controls {
  min-height: 55px;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
}

.game-control {
  border: none;
  border-radius: 4px;
  outline: none;
  margin: 10px;
  padding: 10px;
  cursor: pointer;
  background: #b0b0b0;
  -webkit-transition: background .4s;
  transition: background .4s;
  color: #fff;
}

.game-control:hover {
  background: #cacaca;
}

.game-control:active {
  background: #dbdbdb;
}

.game-control.disabled {
  pointer-events: none;
  opacity: .5;
}

#winscreen {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .8);
  color: #fff;
  font-size: 65px;
}


@media screen and (min-width: 768px) {
  #field {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 500px;
    height: 500px;
    border: 1px solid #aaa;
    border-radius: 2px;
  }
}

@media screen and (max-width: 767px) {
  #field {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    border-radius: 2px;
    width: 210px;
    height: 210px;
  }
}

@media screen and (orientation: landscape) and (max-height: 398px) {
  #square {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .controls {
    width: auto;
  }
}

.tile {
  display: -webkit-inline-flex;
  display: inline-flex;
  font-size: 65px;
  -webkit-flex: 1 0 21%;
          flex: 1 0 21%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border: 1px dotted #ddd;
  -webkit-transition: background .3s;
  transition: background .3s;
}

@media screen and (max-width: 767px) {
  .tile {
    display: -webkit-inline-flex;
    display: inline-flex;
    font-size: 32px;
    -webkit-flex: 1 0 21%;
            flex: 1 0 21%;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border: 1px dotted #ddd;
    -webkit-transition: background .3s;
    transition: background .3s;
  }
}

.tile.t1 {
  background: rgba(128, 0, 0, .1);
}

.tile.t2 {
  background: rgba(0, 128, 0, .1);
}

.tile.t3 {
  background: rgba(0, 0, 128, .1);
}

.tile.t4 {
  background: rgba(128, 128, 0, .1);
}

.tile.t5 {
  background: rgba(128, 0, 128, .1);
}

.tile.t6 {
  background: rgba(128, 128, 128, .1);
}

.tile.t7 {
  background: rgba(255, 0, 0, .1);
}

.tile.t8 {
  background: rgba(0, 255, 0, .1);
}

.tile.t9 {
  background: rgba(0, 0, 255, .1);
}

.tile.t10 {
  background: rgba(255, 255, 0, .1);
}

.tile.t11 {
  background: rgba(255, 0, 255, .1);
}

.tile.t12 {
  background: rgba(0, 255, 255, .1);
}

.tile.t13 {
  background: rgba(128, 255, 128, .1);
}

.tile.t14 {
  background: rgba(128, 255, 255, .1);
}

.tile.t15 {
  background: rgba(255, 128, 255, .1);
}


.tile.t0:hover {
  background: none;
}

.tile:hover {
  background: #ffb0b0;
}

.tile.moveable:hover {
  background: #b0ffb0;
}

#history-bar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  height: 100%;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-right: 1px solid #bcbcbc;
  width: 150px;
  max-width: 150px;
  font-size: 20px;
  overflow-y: scroll;
}

#history-content {
  height: 0;
  min-height: 0;
}

.history-item {
  border-bottom: 1px solid #bcbcbc;
  min-height: 169px;
}

.history-item.current {
  background: rgba(0, 0, 0, .2);
}

.history-item-tile {
  width: 40px;
  height: 40px;
  border: 1px solid #ddd;
}

@media screen and (max-width: 767px) {
  #history-bar {
    width: 100px;
    max-width: 100px;
    font-size: 15px;
  }

  .history-item {
    min-height: 89px;
  }

  .history-item-tile {
    width: 20px;
    height: 20px;
  }
}

#game {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

